import React from "react";
import loadingScreen from "../../../utilities/loadingScreen";
import Form from "../../../components/UI/Form/Form";
import { endPoints } from "../../../services/config";
import axios from "axios";
import { showNotification } from "../../../utilities/fnUtil";
class ForgotPassword extends React.Component {
  state = {
    forgotPasswordForm: {
      telephone: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "S in Thoi",
          name: "S in Thoi"
        },
        value: "",
        validation: {
          required: true,
          minLength: 9,
          maxLength: 32,
          numberValid: "^[0-9]+$",
          errorMessage: "S in thoi khng ng hoc cha c ng k!"
        },
        valid: true
      },
    },
    formIsValid: false
  };


  componentDidMount() {
    loadingScreen.hideLoading();
  }

  componentWillMount() {
    loadingScreen.showLoading();
  }

  setStateForm = (object, submit = false) => {
    this.setState(object, () => {
      if (this.state.formIsValid && submit) {
        this.onForgotPWD();
      }
    });
  };

  onForgotPWD = () => {
    loadingScreen.showLoading();
    const user = {
      userPhone : this.state.forgotPasswordForm.telephone.value,
      origin: window.location.origin
    }

    axios
      .post(endPoints.FORGOT_PASSWORD_API, user)
      .then(res => {
        loadingScreen.hideLoading();
        if(!res) {
          showNotification({
            type: 'error',
            message: 'S in Thoi Khng ng Hoc Cha ng K! Vui Lng Thao Tc Li!'
          })
        } else {
          showNotification({
            message: 'Vui Lng Kim Tra Email  Thay i Mt Khu Theo ng Dn Web!'
          })
          
        }
      }).catch(err => {
        loadingScreen.hideLoading();
        showNotification({
          type: 'error',
          message: 'S in Thoi Khng ng Hoc Cha ng K! Vui Lng Thao Tc Li!'
        })
      })
  };
  render() {
    return (
      <div className="main-content">
        <div id="breadcrumb">
          <div className="container">
            <div className="row">
              <ul className="breadcrumb">
                <h2 className="page-title">QUN MT KHU</h2>
                <li>
                  <a href="/">
                    <i className="fa fa-home" />
                  </a>
                </li>
                <li>
                  <a
                    href="/"
                    style={{ pointerEvents: "none", cursor: "default" }}
                  >
                    Qun Mt Khu
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div id="content" style={{ margin: "auto", width: "60%" }}>
          <p>
            Vui Lng Nhp S in Thoi ca bn. Sau
             Bm Nt Xc Nhn  Nhn Mt Khu Mi Qua E-mail.
          </p>

          <Form
            idForm="forgotPasswordForm"
            nameForm="forgotPasswordForm"
            originalForm={this.state.forgotPasswordForm}
            setState={this.setStateForm}
            btnName="Xc Nhn"
          />

        </div>
      </div>
    );
  }
}

export default ForgotPassword;

