import React from "react";
import loadingScreen from "../../../utilities/loadingScreen";
import Form from "../../../components/UI/Form/Form";
import { Link } from "react-router-dom";
import axios from "axios";
import { endPoints } from "../../../services/config";
import Actions from "../../../redux/rootActions";
import { connect } from "react-redux";
import { showNotification } from "../../../utilities/fnUtil";
class Login extends React.Component {
  state = {
    loginForm: {
      telephone: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "S in Thoi",
          name: "S in Thoi"
        },
        value: "",
        validation: {
          required: true,
          minLength: 9,
          maxLength: 32,
          numberValid: "^[0-9]+$",
          errorMessage: "S in Thoi Khng ng Hoc Cha c ng K!"
        },
        valid: true
      },
      password: {
        elementType: "input",
        elementConfig: {
          type: "password",
          placeholder: "Mt Khu",
          name: "Mt Khu"
        },
        value: "",
        validation: {
          required: true,
          minLength: 1,
          maxLength: 32,
          errorMessage: "Mt Khu Khng ng. Vui Lng Nhp Li!"
        },
        valid: true
      }
    },
    formIsValid: false
  };

  componentDidMount() {
    loadingScreen.hideLoading();
  }
  componentWillMount() {
    loadingScreen.showLoading();
  }

  setStateForm = (object, submit = false) => {
    this.setState(object, () => {
      if (this.state.formIsValid && submit) {
        // loginService.loginLS();
        // this.props.history.push({pathname:'/home'})

        this.onLogin();
      }
    });
  };

  onLogin = () => {
    loadingScreen.showLoading();
    const user = {
      userphone: this.state.loginForm.telephone.value,
      password: this.state.loginForm.password.value
    };
    axios
      .post(endPoints.HOAXUYENVIET_LOGIN, user)
      .then(res => {
        localStorage.setItem("authUser", JSON.stringify(res));
        this.props.updateAuthUser(res);
        this.props.getUserById(res.userPhone);
        this.props.getCartFromSV(res.userPhone);
        this.props.history.push("/account");
        loadingScreen.hideLoading();
      })
      .catch(err => {
        loadingScreen.hideLoading();
        showNotification({
          type: "error",
          message:
            "Khng th ng nhp! Vui lng kim tra li ti khon hoc mt khu!"
        });
      });
  };

  render() {


    return (
      <div className="main-content">
        <div id="breadcrumb">
          <div className="container">
            <div className="row">
              <ul className="breadcrumb">
                <h2 className="page-title">NG NHP</h2>
                <li>
                  <a href="/">
                    <i className="fa fa-home" />
                  </a>
                </li>
                <li>
                  <a
                    href="/"
                    style={{ pointerEvents: "none", cursor: "default" }}
                  >
                    ng Nhp
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div id="content" className="container"  style={{ width: "100%",margin:'0 auto' }}>
          <div className="row">
            <div className="col-sm-6">
                <h2>Khch Hng Mi</h2>
                <p>
                  <strong>ng K</strong>
                </p>
                <p>
                  Bng cch to ti khon, bn s c th mua sm nhanh hn, cp
                  nht trng thi ca n hng v theo di cc n hng bn 
                  thc hin trc .
                </p>
                <Link to="/register" className="btn btn-default">
                  Tip tc
                </Link>
                <div style={{margin: '50px 0'}}>
                  <h2> Qun Mt Khu </h2>     
                  <Link to="/forgotpassword" className="btn btn-default">
                  Tm Li Mt Khu
                  </Link>
                </div>
                
            </div>
            
            <div className="col-sm-6">
              <div className="well">
                <h2>ng Nhp</h2>
                <p>
                  <strong>Cho mng bn quay tr li!</strong>
                </p>
                <Form
                  idForm="loginForm"
                  nameForm="loginForm"
                  originalForm={this.state.loginForm}
                  setState={this.setStateForm}
                  btnName="ng nhp"
                />
              </div>
            </div>
            
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    authUser: state.authUser
  };
};
const mapDispatchToProps = dispatch => {
  return {
    updateAuthUser: authUser =>
      dispatch(Actions.authActions.getAuthUser(authUser)),
    getUserById: id => dispatch(Actions.userActions.getUserFromSV(id)),
    getCartFromSV: id => dispatch(Actions.userActions.getCartFromSV(id))
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Login);
